.community{

    >figure{
        width: 100%;
        height: 100%;
        position: relative; 
        margin-top: -100px;
        background-image: url(../../../public/img/youtube01.jpg);
        background-size: cover;
        background-position: center bottom;
        background-repeat: no-repeat
        ;
        filter: saturate(50%);
        overflow: hidden;

        > .inner{
            width: 1180px;
            height: 100%;
            position: relative;
            margin: 0 auto;  
            padding:150px 0px 100px;

            .title{
                // border:3px solid blue;
                margin-bottom:30px;

                h1{
                    font:bold 175px/1 'Orbitron';
                    color:rgba(255,255,255,.05);
                    letter-spacing: 7px;
                    text-align:right;
                    margin-bottom:50px;
                }
        
               > h2{
                    font:bold 50px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing: 5px;
                    width: 100%;
                    margin-bottom:15px;
                    text-align: center;
                    position: relative;

                    &::before{
                        content: '';
                        display: block;
                        position: absolute;
                        width: 20%;
                        height: 15px;
                        top: 50%;
                        transform: translateY(-50%);
                        right:24%;
                        background: #cc9e67;
                        opacity: 0.7;
                        z-index:-1;
                    }
        
                }
        
                >p{
                    font:normal 13px/1 'Orbitron';
                    color:rgba(255,255,255,.8);
                    letter-spacing: 5px;
                    width: 100%;
                    text-align:center;
                }
            }


            .board{
                width: 100%;
                height: 100%;

                .boardMain{
                    width: 100%;
                    height: 100%;
                    padding:25px;
                    transition: 1s;
                    position: relative;

                    .inputBox{
                        margin-bottom: 25px;
                        // border:2px solid yellow;
                        background: rgba(255,255,255,.9);
                        box-shadow: 5px 5px 10px rgba(0,0,0,.9);

                        strong{
                            font:bold 50px/60px 'Orbitron';
                            color:rgba(0,0,0,.9);
                            letter-spacing: 3px;
                            margin-bottom: 35px;
                            position: absolute;
                            top: 7.5%;
                            left: 7.5%;
                            transform: translate(-10%,-50%);
                        }
    
                        input[type=text]{
                            width: 27.5%;
                            height: 30px;
                            font:bold 11px/1 'Orbitron';
                            letter-spacing: 1.3px;
                            padding-left: 10px;
                            position: absolute;
                            top:12.5%;
                            left: 8%;
                            transform: translate(-10%,-50%);
                        }
    
                        textarea{
                            width:62.5%;
                            height: 200px;
                            margin-left:35%;
                            margin-bottom: 30px;
                            font:bold 11px/1 'Orbitron';
                            letter-spacing: 1.3px;
                            padding:10px 0px 0px 10px;
                        }
    
                        button{
                            width: 100px;
                            height: 30px;
                            margin-right: 50px;
                            font:bold 10px/1 'Orbitron';
                            color:#111;
                            position: relative;
                            left: 35%;
                            transform: translateX(-30%);
                            letter-spacing: 2.3px;
                            cursor: pointer;
                            transition: 0.5s;
                            border:none;
                            margin-bottom: 30px;
                            margin-left:25%;
                            border:1px solid #111;

                            &:nth-of-type(2){
                                margin-right: 0;
                                margin-left: 0;
                            }
    
                            &:hover{
                                background: rgba(0,0,0,.5);
                                color:rgba(255,255,255,.9);
                            }
                        }
                    }

                    .showList{
                        display: flex;
                        flex-wrap: wrap;
                        justify-content:space-between;
                        align-items: center;
                        height: 100%;
                        overflow: hidden;

                        article{
                            width:47.5%;
                            margin-bottom: 30px;
                            position: relative;
                            margin-right: 1%;
                            background: rgba(255,255,255,.9);
                            box-shadow: 5px 5px 10px rgba(0,0,0,.9);
                            padding-bottom: 30px;

                            &:nth-child(2n){
                                margin-right: 0;
                            }
    
                            .post{
                                width: 100%;
                                height: 200px;
                                padding:15px 15px 25px 15px;
                                margin-bottom: 20px;
                                transition: 1s;
    
                                input[type=text]{
                                    font:bold 10px/1 'Orbitron';
                                    color:rgba(0,0,0,.9);
                                    letter-spacing: 2.3px;
                                    width: 100%;
                                    height: 20%;
                                    margin-bottom: 10px;
                                    padding:0px 0px 0px 10px;
                                }
    
                                textarea{
                                    font:normal 10px/1 'Orbitron';
                                    color:rgba(0,0,0,.8);
                                    letter-spacing: 2.3px;
                                    width: 100%;
                                    height: 80%;
                                    padding:10px 0px 0px 10px;
                                }
    
                                h3{
                                    font:bold 10px/1 'Orbitron';
                                    color:rgba(0,0,0,.9);
                                    letter-spacing: 2.3px;
                                    width: 100%;
                                    height: 20%;
                                    margin-bottom: 10px;
                                    padding:0px 0px 0px 10px;
                                    border-bottom: 2px solid rgba(0,0,0,.3);
                                    position: relative;

                                    &::before{
                                        content: '';
                                        display: block;
                                        position: absolute;
                                        width: 10px;
                                        height: 10px;
                                        background: #f9bf7c;
                                        left:-2.5px;
                                        top: 0px;
                                    }
                                }
    
                                p{
                                    font:normal 10px/15px 'Orbitron';
                                    color:rgba(0,0,0,.8);
                                    letter-spacing: 2.3px;
                                    width: 100%;
                                    height: 80%;
                                    padding:10px 0px 0px 10px;
                                }
                                
                            }
    
                            .btns{
                                text-align: center;
    
                                button{
                                    font:bold 10px/1 'Orbitron';
                                    color:rgba(0,0,0,.9);
                                    letter-spacing: 3px;
                                    background: rgba(255,255,255,.9);
                                    border:none;
                                    width: 85px;
                                    height: 30px;
                                    padding:5px;
                                    margin-right: 10px;
                                    cursor: pointer;

                                    &:nth-child(2){
                                        margin-right: 0px;
                                    }
    
                                    &:hover{
                                        background: rgba(255,255,255,.9);
                                        color:rgba(0,0,0,.9);
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        
        .custom-shape-divider-top-1645429182 {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            overflow: hidden;
            line-height: 0;
            transform: rotate(180deg);
            opacity: 0.5;
        }
        
        .custom-shape-divider-top-1645429182 svg {
            position: relative;
            display: block;
            width: calc(107% + 1.3px);
            height: 195px;
        }
        
        .custom-shape-divider-top-1645429182 .shape-fill {
            fill: #1E2C4980;
        }

        >img{
            object-fit: contain;
            position: absolute;
            z-index:1;
            bottom: -100%;

            &:nth-of-type(1){
                left: 10%;
                width: 90px;
                height: 90px;
                animation: locationAni01 30s linear infinite;
            }
    
            &:nth-of-type(2){
                left: 25%;
                width: 100px;
                height: 100px;
                animation: locationAni02 30s  5s linear infinite;
            }
    
            &:nth-of-type(3){
                left: 50%;
                width: 75px;
                height: 75px;
                animation: locationAni03 40s 7s linear infinite;
            }
    
            &:nth-of-type(4){
                left: 75%;
                width: 65px;
                height: 65px;
                animation: locationAni04 35s  4s linear infinite;
            }

            &:nth-of-type(5){
                left: 90%;
                width: 120px;
                height: 120px;
                animation: locationAni05 30s 2s linear infinite;
            }
        }
    }
}


@keyframes locationAni01{
    0%{bottom: -200%; left: 10%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.5;}
    50%{opacity: 0.5; }
    75%{bottom: 100%; left: 25%; opacity: 0;}
    100%{bottom:0%;   opacity: 0;}
}

@keyframes locationAni02{
    0%{bottom: -200%; left: 25%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.1;}
    50%{opacity: 0.1;}
    75%{bottom: 80%;  left: 50%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni03{
    0%{bottom: -200%; left: 50%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.2;}
    50%{opacity: 0.2; }
    75%{bottom: 120%; left: 35%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni04{
    0%{bottom: -200%; left: 75%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.4;}
    50%{opacity: 0.4; }
    75%{bottom: 100%; left: 60%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes locationAni05{
    0%{bottom: -200%; left: 90%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.3;}
    50%{opacity: 0.3;}
    75%{bottom: 70%;  left: 80%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}
