#board{
    width: 100%;
    padding: 250px 0px 150px;
    background-image: url(../../../public/img/newsBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow: hidden;
    filter: saturate(70%);

    .explanation11{
        position: absolute;
        left: 150px;
        bottom: 100px;
        width: 200px;
        height: 200px;

        .exP11{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(-90deg);
  
        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:90%;
            transform: rotate(90deg);
            }
  
          &::after{
              content: '∨';
              display: block;
              font-size: 20px;
              color:rgba(255,255,255,.5);
              position: absolute;
              bottom:78%;
              right:125%;
              transform: rotate(-270deg);
              }
          }
      }


    .inner{
        width: 1180px;
        margin: 0 auto;
        // border:4px solid yellow;

        strong{
            position: absolute;
            top:5%;
            left:17.7%;
            font:bold 250px/1 'Orbitron';
            color:rgba(255,255,255,.05);
            letter-spacing: 7px;
            transition: 2s;
        }

        >h2{
            font:bold 85px/1 'Orbitron';
            color:rgba(255,255,255,.8);
            letter-spacing: 7px;
            text-align: center;
            margin-bottom: 30px;
            text-align: center;
            transition: 2s;
        }

        ul{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-content: flex-start;
            // border: 3px solid red;
            padding:100px 0px 50px;

            li{
                width: 48%;
                height: 200px;
                padding: 40px;
                box-shadow: 3px 3px 3px rgba(0,0,0,.9);
                margin-bottom: 20px;
                // background: #1E2831;
                background: #23323b;
                cursor: pointer;

                h2{
                    font:bold 18px/1 'Orbitron';
                    letter-spacing: 2px;
                    color:rgba(255,255,255,.9);
                    margin-bottom: 10px;
                }

                p{
                    font:normal 12px/2.3 'Orbitron';
                    letter-spacing: 2.3px;
                    color:rgba(255,255,255,.7);
                }
            }
        }
    }
}