@import url('https://fonts.googleapis.com/css2?family=Shippori+Antique+B1&family=Zhi+Mang+Xing&display=swap');

#map{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/mapBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 50px 0px;
    filter: saturate(70%);

    >strong{
        position: absolute;
        font:bold 175px/1 'Orbitron';
        color:rgba(255,255,255,.02);
        letter-spacing: 10px;
        top:-10%;
        right: -300px;
        transform: rotate(90deg);
    }

    .inner{
        margin: 0 auto;
        width: 1180px;
        height: 80%;
        position: relative;
        border:1px solid rgba(255,255,255,.1);
        background: #1e2831;
        padding-left: 45px;
        padding-top: 20px;

        .wrap{
            width: 100%;
            height: 700px;
            padding:50px 0px;
            display: flex;
            flex-direction: row;

            .mapList{
                width: 50%;
                height: 100%;
                padding:50px 0px;

                h2{
                    font:bold 100px/1 'Orbitron';
                    color:rgba(255,255,255,.7);
                    letter-spacing: 10px;
                    position: absolute;
                    top: -50px;
                }

                #maps{
                    width: 100%;
                    height: 100%;
                    filter: grayscale(100%);

                    .mapBox{
                        div{

                        }
                    }
                }
            }

            .contactForm{
                width: 50%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;
                padding-left: 75px;


                h2{
                    font:bold 50px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing:5px;
                    margin-bottom: 30px;
                }

                ul{
                    text-align: left;
                    margin-bottom: 30px;

                    li{
                        strong{
                            font:bold 15px/1 'Orbitron';
                            color:rgba(255,255,255,.8);
                            letter-spacing:2.3px;
                            margin-bottom: 15px;
                            display: block;
                        }

                        p{
                            font:normal 12px/1 'Orbitron';
                            color:rgba(255,255,255,.7);
                            letter-spacing:2.3px;
                            margin-bottom: 10px;
                        }

                        span{
                            font:normal 12px/1 'Orbitron';
                            color:rgba(255,255,255,.7);
                            letter-spacing:2.3px;
                        }
                    }
                }
                
                h3{
                    font:bold 25px/1 'Orbitron';
                    color:rgba(255,255,255,.8);
                    letter-spacing:5px;
                    margin-bottom: 25px;
                }

                form{
                    width: 85%;
                    
                    tbody{
                        width: 100%;
                        height: 100%;

                        tr{

                            td{

                                input{
                                    width: 450px;
                                    height: 40px;
                                    margin-bottom: 20px;
                                    font:bold 10px/1 'Orbitron';
                                    letter-spacing:1.8px;
                                    color:#111;
                                    padding:10px;
                                }

                                textarea{
                                    width: 450px;
                                    margin-bottom: 15px;
                                    font:bold 10px/1 'Orbitron';
                                    letter-spacing:1.8px;
                                    color:#111;
                                    padding:10px;
                                }
                            }

                            .btnSet2{

                                input{
                                    width: 100px;
                                    height: 30px;
                                    border:none;
                                    background: rgba(255,255,255,.9);
                                    color:rgba(0,0,0,.9);
                                    cursor: pointer;
                                    font:bold 11px/1 'Orbitron';
                                    letter-spacing:1.8px;
                                    transition: 1s;

                                    &:hover{
                                        background: rgba(0,0,0,.9);
                                        color:rgba(255,255,255,.9);
                                    }
                                }
                            }
                        }
                    }
       
                }
            }
        }
    }

    .custom-shape-divider-top-1645429182 {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
        transform: rotate(180deg);
        opacity: 0.5;
    }
    
    .custom-shape-divider-top-1645429182 svg {
        position: relative;
        display: block;
        width: calc(107% + 1.3px);
        height: 200px;
    }
    
    .custom-shape-divider-top-1645429182 .shape-fill {
        fill: #23323b;
        opacity: 0.3;
    }

    >img{
        object-fit: cover;
        position: absolute;
        z-index:1;

        &:nth-of-type(1){
            width: 60px;
            height: 60px;
            left: -100%;
            animation: mapAni01 30s linear infinite;
        }

        &:nth-of-type(2){
            width: 50px;
            height: 50px;
            left: -100%;
            animation: mapAni02 30s linear infinite;
        }

        &:nth-of-type(3){
            width: 65px;
            height: 65px;
            left: -100%;
            animation: mapAni03 35s 3s linear infinite;
        }

        &:nth-of-type(4){
            width: 45px;
            height: 45px;
            left: -100%;
            animation: mapAni04 35s  3s linear infinite;
        }
    }
}

@keyframes mapAni01{
    0%{top:100px; left: -100%; opacity: 0;}
    50%{top:-150px; opacity: 0.5;}
    75%{top:50px; left: 90%; opacity: 0;}
    100%{top:100px;   opacity: 0;}
}

@keyframes mapAni02{
    0%{top:100px; left: -100%; opacity: 0;}
    50%{top:-180px; opacity: 0.5;}
    75%{top:70px; left: 85%; opacity: 0;}
    100%{top:100px;   opacity: 0;}
}

@keyframes mapAni03{
    0%{bottom:0px; left: -100%; opacity: 0;}
    50%{bottom:150px; opacity: 0.5;}
    75%{bottom:50px; left: 90%; opacity: 0;}
    100%{bottom:80px;   opacity: 0;}
}

@keyframes mapAni04{
    0%{bottom:10px; left: -100%; opacity: 0;}
    50%{bottom:140px; opacity: 0.5;}
    75%{bottom:60px; left: 85%; opacity: 0;}
    100%{bottom:80px;   opacity: 0;}
}

